@import "./variables";
@import "./libs/normalize";
@import "components/all";

body {
	background-color: $background;
}

.wrapper {
	width: calc(100vw - 100px);
	height: 100%;
	background-color: rgb(255, 255, 255);
	margin: 50px auto;
	border-radius: 10px;
	max-width: 1400px;
	@media (max-width: 768px) {
		margin: 0px auto;
		width: calc(100vw - 0px);
	}
}

.content {
	padding: 40px 0;
	@media (max-width: 768px) {
		padding: 20px 0;
	}
	&__title {
		margin: 35px 0;
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
		gap: 40px;
		justify-items: center;
	}
	&__list {
	}
	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
	}
}

.container {
	width: $container-width;
	margin: 40px auto;

	&--cart {
		max-width: 820px;

		.content__title {
			margin: 0;
		}
		@media (max-width: 768px) {
			margin: 30px auto;
		}
	}
}

.cart {
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width: 400px) {
			align-items: start;
			flex-direction: column;
		}
	}

	.content__title {
		display: flex;
		align-items: center;
		font-size: 32px;
		@media (max-width: 400px) {
			margin-bottom: 20px;
		}
		svg {
			position: relative;
			top: -2px;
			width: 30px;
			height: 30px;
			margin-right: 10px;
			path {
				stroke: $black;
				strokewidth: 1.9;
			}
		}
	}

	&__clear {
		display: flex;
		align-items: center;
		cursor: pointer;
		@include noselect();

		span {
			display: inline-block;
			margin-left: 7px;
			color: #b6b6b6;
			font-size: 18px;
		}

		span,
		svg,
		path {
			transition: all $duration ease-in-out;
		}

		&:hover {
			svg {
				path {
					stroke: darken($color: #b6b6b6, $amount: 50);
				}
			}
			span {
				color: darken($color: #b6b6b6, $amount: 50);
			}
		}
	}

	&__item {
		position: relative;
		display: flex;
		width: 100%;
		border-top: 1px solid $gray-line;
		padding-top: 30px;
		margin-top: 30px;
		@media (max-width: 768px) {
			flex-direction: column;
			align-items: center;
		}
		&-img {
			display: flex;
			align-items: center;

			width: 10%;
			@media (max-width: 768px) {
				width: 100%;
				justify-content: center;
			}
			img {
				width: 80px;
				height: 80px;
				@media (max-width: 768px) {
					width: 160px;
					height: 160px;
				}
			}
		}

		&-info {
			display: flex;
			flex-direction: column;
			justify-content: center;
			width: 40%;
			@media (max-width: 768px) {
				width: 100%;
				align-items: center;
				margin: 10px 0;
			}

			h3 {
				font-weight: bold;
				font-size: 22px;
				line-height: 27px;
				letter-spacing: 0.01em;
			}

			p {
				font-size: 18px;
				color: #8d8d8d;
			}
		}

		&-count {
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 13%;
			@media (max-width: 768px) {
				width: 40%;
				justify-content: space-between;
				margin-bottom: 15px;
			}
			&-minus {
				svg {
					path:first-of-type {
						display: none;
					}
				}
			}

			b {
				font-size: 22px;
			}
		}

		&-price {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 33%;

			b {
				font-weight: bold;
				font-size: 22px;
				letter-spacing: 0.01em;
			}
		}

		&-remove {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			width: 4%;
			@media (max-width: 768px) {
				position: absolute;
				top: 10px;
				right: 10px;
			}
			.button {
				border-color: darken($color: $gray-line, $amount: 10);
			}

			svg {
				transform: rotate(45deg);

				path {
					fill: darken($color: $gray-line, $amount: 15);
				}
			}

			.button {
				svg {
					width: 11.5px;
					height: 11.5px;
					position: relative;
				}
				&:hover,
				&:active {
					border-color: darken($color: $gray-line, $amount: 80);
					background-color: darken($color: $gray-line, $amount: 80);
				}
			}
		}
	}

	&__bottom {
		margin: 50px 0;
padding: 5px;
		&-details {
			display: flex;
			justify-content: space-between;
			@media (max-width: 490px) {
				flex-direction: column;
				align-items: center;
			}

			span {
				font-size: 22px;
				@media (max-width: 490px) {
					margin-bottom: 10px;
				}
				&:last-of-type {
					b {
						color: $orange;
					}
				}
			}
		}

		&-buttons {
			display: flex;
			justify-content: space-between;
			margin-top: 40px;
			@media (max-width: 490px) {
				margin-top: 20px;
				
			}
			.go-back-btn {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 210px;
				@media (max-width: 490px) {
					margin-right: 20px;
					
				}
				border-color: darken($color: $gray-line, $amount: 10);

				span {
					color: darken($color: $gray-line, $amount: 20);
					font-weight: 500;
					font-weight: 600;
				}

				&:hover {
					background-color: darken($color: $gray-line, $amount: 90);
					border-color: darken($color: $gray-line, $amount: 90);

					span {
						color: $gray-line;
					}
				}

				svg {
					margin-right: 12px;
					path {
						fill: transparent;
						strokewidth: 2;
					}
				}
			}

			.pay-btn {
				font-size: 16px;
				font-weight: 600;
				width: 210px;
				
				@media (max-width:490px){
					padding: 5px;
				 
			 }
			}
		}
	}

	&--empty {
		margin: 100px auto 0 auto;
		width: 560px;
		text-align: center;

		h2 {
			font-size: 32px;
			margin-bottom: 10px;
		}

		p {
			font-size: 18px;
			line-height: 145.4%;
			letter-spacing: 0.01em;
			color: #777777;
		}

		icon {
			position: relative;
			top: 2px;
		}

		img {
			display: block;
			width: 300px;
			margin: 45px auto 60px;
		}

		.button--black {
			padding: 12px 0 14px; 
			@media (max-width:490px){
				padding: 5px 0 5px; 
			 
		 }
			width: 230px;
			margin: 0 auto;
			font-weight: 600;
			font-size: 18px;
		}
	}
}
