@import "../variables";

.categories {
	ul {
		display: flex;
		flex-wrap: wrap;
		li {
			background-color: #f9f9f9;
			padding: 13px 30px;
			border-radius: 30px;
			margin-right: 10px;
			font-weight: bold;
			margin-bottom: 15px;
			cursor: pointer;
			transition: background-color 0.1s ease-in-out;
			@include noselect();
			@media (max-width: 768px) {
				padding: 7px 20px;
				margin-right: 5px;
			}
			&:hover {
				background-color: darken(#f9f9f9, 2%);
			}

			&:active {
				background-color: darken(#f9f9f9, 5%);
			}

			&.active {
				background-color: #282828;
				color: #fff;
			}
		}
	}
}
